* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h2{
  padding-bottom: 30px;
}

h3{
  padding-bottom: 20px;
}

li{
  padding-bottom: 10px;
}

p{
  padding-bottom: 20px;
}

body, html {
  font-family: 'Courier New', Courier, monospace;
  background-color: #031326;
  color: #07f57a;
  height: 100%;
}

/* Container */
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

/* Header */
.header {
  margin-top: 20px;
  text-align: center;
  border: 1px solid #07f57a;
  padding: 20px;
  width: 100%;  /* Adjusted for laptop responsiveness */
  color: #07f57a;
}

/* Main Content - Flexbox layout */
.main-content {
  display: flex;
  /* justify-items: center; */

  flex-direction: row;
  justify-content:center;
  margin-top: 20px;
  width: 100%;  /* Full width for larger screens */
}

/* Content Box */
.content {
  border: 1px solid #07f57a;
  background-color: #07f57a;
  padding: 20px;
  width: 30%;  /* Adjusted width */
  margin-right: 150px;
  color: #07f57a;
}

/* Sections */
.right-sections {
  display: flex;
  flex-direction: column;
  width: 100%;  /* Adjusted width */
}

.section {
  border: 1px solid #07f57a;
  padding: 20px;
  justify-content:end;
  margin-bottom: 20px;
  width: 100%;  /* Full width inside flex container */
}

/* Colors for each section */
.section:nth-child(1) {
  color: #dfbf0b;
  border-color: #dfbf0b;
}

.section:nth-child(2) {
  color: #91bdf0;
  border-color: #91bdf0;
}

ul{
  margin-left: 20px;
}
/* section:nth-child(2) {
  color: #0776f5;
  border-color: #0776f5;
} */

.section:nth-child(3) {
  color: #e4988f;
  border-color: #e4988f;
}

/* .section:nth-child(3) {
  color: #e36c5f;
  border-color: #e36c5f;
} */

.section:nth-child(4) {
  color: #07f57a;
  border-color: #07f57a;
}

.section.section.skills{
  color: #0776f5;
  width: 10%;
}
/* Make it Responsive */
@media (max-width: 768px) {
  .main-content {
    flex-direction: column;
    width: 100%;
  }

  .content {
    width: 50%;  /* Full width for mobile */
    margin-bottom: 20px;
  }

  .right-sections {
    width: 100%;  /* Full width for mobile */
  }

  .section {
    width: 100%;  /* Full width for mobile */
  }

  .header {
    width: 100%;  /* Full width for mobile */
  }
}
